import { Injectable, OnDestroy, inject } from '@angular/core';
import { BehaviorSubject, Observable, Subject, takeUntil } from 'rxjs';
import { SlackChannel, SlackMessage } from '../models/slack.interface';
import { SlackApiService } from '../services/slack-api.service';
import { SlackSocketAdapter } from '../services/slack-socket.adapter';
import { DebugLoggerService } from '../../../shared/services/debug-logger.service';

/**
 * Channel ViewModel
 * MVVM íŒ¨í„´ì˜ ViewModel ë ˆì´ì–´
 * ì±„ë„ ê´€ë ¨ ë¹„ì¦ˆë‹ˆìŠ¤ ë¡œì§ ê´€ë¦¬
 */
@Injectable()
export class ChannelViewModel implements OnDestroy {
  private destroy$ = new Subject<void>();
  private debugLogger = inject(DebugLoggerService);

  // ìƒíƒœ ê´€ë¦¬
  private channels$ = new BehaviorSubject<SlackChannel[]>([]);
  private activeChannel$ = new BehaviorSubject<SlackChannel | null>(null);
  private isLoading$ = new BehaviorSubject<boolean>(false);

  constructor(
    private apiService: SlackApiService,
    private socketAdapter: SlackSocketAdapter
  ) {
    this.debugLogger.info('SlackChannel', 'ğŸš€ ChannelViewModel ì´ˆê¸°í™”');
    this.initializeSocketListeners();
  }

  /**
   * Socket ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì´ˆê¸°í™”
   */
  private initializeSocketListeners(): void {
    this.debugLogger.info('SlackChannel', 'ğŸ‘‚ Socket ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì´ˆê¸°í™”');
    
    // ì±„ë„ ìƒì„± ì´ë²¤íŠ¸
    this.socketAdapter.onChannelCreated$
      .pipe(takeUntil(this.destroy$))
      .subscribe(channel => {
        this.debugLogger.success('SlackChannel', `âœ… ìƒˆ ì±„ë„ ìƒì„±ë¨: ${channel.name} (ID: ${channel.id})`);
        const current = this.channels$.value;
        this.channels$.next([...current, channel]);
      });

    // ì±„ë„ ì—…ë°ì´íŠ¸ ì´ë²¤íŠ¸
    this.socketAdapter.onChannelUpdated$
      .pipe(takeUntil(this.destroy$))
      .subscribe(updatedChannel => {
        this.debugLogger.info('SlackChannel', `ğŸ“ ì±„ë„ ì—…ë°ì´íŠ¸ë¨: ${updatedChannel.name}`);
        const current = this.channels$.value;
        const index = current.findIndex(c => c.id === updatedChannel.id);
        
        if (index !== -1) {
          const updated = [...current];
          updated[index] = updatedChannel;
          this.channels$.next(updated);

          // í™œì„± ì±„ë„ ì—…ë°ì´íŠ¸
          if (this.activeChannel$.value?.id === updatedChannel.id) {
            this.activeChannel$.next(updatedChannel);
          }
        }
      });
  }

  /**
   * ëª¨ë“  ì±„ë„ ë¡œë“œ
   */
  loadChannels(): void {
    this.debugLogger.info('SlackChannel', 'ğŸ“ ì±„ë„ ëª©ë¡ ë¡œë“œ ì‹œì‘');
    this.isLoading$.next(true);

    this.apiService.getChannels()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (channels) => {
          this.debugLogger.success('SlackChannel', `âœ… ì±„ë„ ëª©ë¡ ë¡œë“œ ì„±ê³µ: ${channels.length}ê°œ`);
          this.debugLogger.info('SlackChannel', `ì±„ë„ ëª©ë¡: ${JSON.stringify(channels.map(c => c.name))}`);
          this.channels$.next(channels);
          this.isLoading$.next(false);
        },
        error: (error) => {
          this.debugLogger.error('SlackChannel', `âŒ ì±„ë„ ëª©ë¡ ë¡œë“œ ì‹¤íŒ¨: ${error.message}`);
          this.isLoading$.next(false);
        }
      });
  }

  /**
   * ì±„ë„ ì„ íƒ
   */
  selectChannel(channelId: string): void {
    this.debugLogger.info('SlackChannel', `ğŸ¯ ì±„ë„ ì„ íƒ: ${channelId}`);
    this.isLoading$.next(true);

    this.apiService.getChannel(channelId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (channel) => {
          this.debugLogger.success('SlackChannel', `âœ… ì±„ë„ ì„ íƒ ì„±ê³µ: ${channel.name}`);
          this.activeChannel$.next(channel);
          this.socketAdapter.joinChannel(channelId);
          this.isLoading$.next(false);
        },
        error: (error) => {
          this.debugLogger.error('SlackChannel', `âŒ ì±„ë„ ì„ íƒ ì‹¤íŒ¨: ${error.message}`);
          this.isLoading$.next(false);
        }
      });
  }

  /**
   * ì±„ë„ ìƒì„±
   */
  createChannel(name: string, description: string, isPrivate: boolean, members: string[]): void {
    this.debugLogger.info('SlackChannel', 'ğŸ”¨ ì±„ë„ ìƒì„± ì‹œì‘');
    this.debugLogger.info('SlackChannel', `ì±„ë„ëª…: ${name}`);
    this.debugLogger.info('SlackChannel', `ì„¤ëª…: ${description}`);
    this.debugLogger.info('SlackChannel', `ë¹„ê³µê°œ: ${isPrivate}`);
    this.debugLogger.info('SlackChannel', `ë©¤ë²„ ìˆ˜: ${members.length}ëª…`);
    this.debugLogger.info('SlackChannel', `ë©¤ë²„ IDs: ${JSON.stringify(members)}`);
    
    this.isLoading$.next(true);

    const channelData = { name, description, isPrivate, members };
    this.debugLogger.info('SlackChannel', `API í˜¸ì¶œ ë°ì´í„°: ${JSON.stringify(channelData)}`);

    this.apiService.createChannel(channelData)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (channel) => {
          this.debugLogger.success('SlackChannel', `âœ… ì±„ë„ ìƒì„± ì„±ê³µ: ${channel.name} (ID: ${channel.id})`);
          this.debugLogger.info('SlackChannel', `ìƒì„±ëœ ì±„ë„ ë°ì´í„°: ${JSON.stringify(channel)}`);
          // Socket ì´ë²¤íŠ¸ë¡œ ìë™ ì¶”ê°€ë¨
          this.selectChannel(channel.id);
          this.isLoading$.next(false);
        },
        error: (error) => {
          this.debugLogger.error('SlackChannel', `âŒ ì±„ë„ ìƒì„± ì‹¤íŒ¨`);
          this.debugLogger.error('SlackChannel', `ì—ëŸ¬ ë©”ì‹œì§€: ${error.message}`);
          this.debugLogger.error('SlackChannel', `ì—ëŸ¬ ìƒíƒœ: ${error.status}`);
          this.debugLogger.error('SlackChannel', `ì—ëŸ¬ ì „ì²´: ${JSON.stringify(error)}`);
          this.isLoading$.next(false);
        }
      });
  }

  /**
   * ì±„ë„ ì‚­ì œ
   */
  deleteChannel(channelId: string): void {
    this.apiService.deleteChannel(channelId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: () => {
          const current = this.channels$.value;
          this.channels$.next(current.filter(c => c.id !== channelId));

          if (this.activeChannel$.value?.id === channelId) {
            this.activeChannel$.next(null);
          }
        },
        error: (error) => {
        }
      });
  }

  /**
   * ì±„ë„ì— ë©¤ë²„ ì¶”ê°€
   */
  addMember(channelId: string, userId: string): void {
    this.apiService.addMember(channelId, userId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (updatedChannel) => {
          // Socket ì´ë²¤íŠ¸ë¡œ ìë™ ì—…ë°ì´íŠ¸ë¨
        },
        error: (error) => {
        }
      });
  }

  /**
   * ì±„ë„ì—ì„œ ë©¤ë²„ ì œê±°
   */
  removeMember(channelId: string, userId: string): void {
    this.apiService.removeMember(channelId, userId)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (updatedChannel) => {
          // Socket ì´ë²¤íŠ¸ë¡œ ìë™ ì—…ë°ì´íŠ¸ë¨
        },
        error: (error) => {
        }
      });
  }

  // Getters
  getChannels(): Observable<SlackChannel[]> {
    return this.channels$.asObservable();
  }

  getActiveChannel(): Observable<SlackChannel | null> {
    return this.activeChannel$.asObservable();
  }

  getIsLoading(): Observable<boolean> {
    return this.isLoading$.asObservable();
  }

  ngOnDestroy(): void {
    // í™œì„± ì±„ë„ì—ì„œ ë‚˜ê°€ê¸°
    const activeChannel = this.activeChannel$.value;
    if (activeChannel) {
      this.socketAdapter.leaveChannel(activeChannel.id);
    }

    this.destroy$.next();
    this.destroy$.complete();
  }
}
