import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { CryptoUtil } from '../../../core/utils/crypto.util';

@Injectable({
  providedIn: 'root'
})
export class SlackAuthService {
  private readonly STORAGE_KEY = 'slack_auth_token';
  private readonly SESSION_KEY = 'slack_session_timestamp';
  private readonly SESSION_DURATION = 8 * 60 * 60 * 1000; // 8ì‹œê°„
  
  private isAuthenticatedSubject = new BehaviorSubject<boolean>(this.checkAuth());
  public isAuthenticated$: Observable<boolean> = this.isAuthenticatedSubject.asObservable();

  // environmentì—ì„œ ì•”í˜¸í™”ëœ ì¸ì¦ ì •ë³´ë¥¼ ë³µí˜¸í™”í•˜ì—¬ ì‚¬ìš©
  private readonly AUTH_HASH = this.simpleHash(
    CryptoUtil.decrypt(environment.auth.username) + 
    CryptoUtil.decrypt(environment.auth.password)
  );
  
  constructor(private router: Router) {
    this.checkSessionExpiry();
  }

  /**
   * ë¡œê·¸ì¸ ê²€ì¦ - environmentì—ì„œ ì¤‘ì•™ ê´€ë¦¬
   */
  login(username: string, password: string): boolean {
    const inputHash = this.simpleHash(username + password);
    
    if (inputHash === this.AUTH_HASH) {
      const timestamp = Date.now().toString();
      sessionStorage.setItem(this.STORAGE_KEY, 'authenticated');
      sessionStorage.setItem(this.SESSION_KEY, timestamp);
      this.isAuthenticatedSubject.next(true);
      return true;
    }
    
    return false;
  }

  /**
   * ë¡œê·¸ì•„ì›ƒ
   */
  logout(): void {
    console.log('[SlackAuth] ë¡œê·¸ì•„ì›ƒ');
    
    try {
      sessionStorage.removeItem(this.STORAGE_KEY);
      sessionStorage.removeItem(this.SESSION_KEY);
      this.isAuthenticatedSubject.next(false);
    } catch (error) {
      console.error('[SlackAuth] ë¡œê·¸ì•„ì›ƒ ì¤‘ ì˜¤ë¥˜:', error);
    } finally {
      // ì˜¤ë¥˜ ë°œìƒ ì—¬ë¶€ì™€ ê´€ê³„ì—†ì´ í•­ìƒ Slack ë¡œê·¸ì¸ í˜ì´ì§€ë¡œ ì´ë™
      this.router.navigate(['/slack/login']).catch(err => {
        console.error('[SlackAuth] ë„¤ë¹„ê²Œì´ì…˜ ì˜ˆì™¸ ë°œìƒ:', err);
        window.location.hash = '#/slack/login';
      });
    }
  }

  /**
   * ì¸ì¦ ìƒíƒœ í™•ì¸
   */
  isAuthenticated(): boolean {
    return this.checkAuth();
  }

  /**
   * ì„¸ì…˜ ìœ íš¨ì„± ê²€ì‚¬
   */
  private checkAuth(): boolean {
    const token = sessionStorage.getItem(this.STORAGE_KEY);
    const timestamp = sessionStorage.getItem(this.SESSION_KEY);
    
    if (!token || !timestamp) {
      return false;
    }

    // ì„¸ì…˜ ë§Œë£Œ í™•ì¸
    const sessionStart = parseInt(timestamp, 10);
    const now = Date.now();
    
    if (now - sessionStart > this.SESSION_DURATION) {
      this.logout();
      return false;
    }

    return token === 'authenticated';
  }

  /**
   * ì„¸ì…˜ ë§Œë£Œ ì²´í¬ (1ë¶„ë§ˆë‹¤)
   */
  private checkSessionExpiry(): void {
    setInterval(() => {
      if (!this.checkAuth() && this.isAuthenticatedSubject.value) {
        this.logout();
      }
    }, 60000);
  }

  /**
   * ê°„ë‹¨í•œ í•´ì‹œ í•¨ìˆ˜
   */
  private simpleHash(str: string): string {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      const char = str.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash;
    }
    return hash.toString(36);
  }
}
