import { Component, Input, Output, EventEmitter } from '@angular/core';
import { SlackMessage } from '../../models/slack.interface';

/**
 * ë©”ì‹œì§€ ì¹´ë“œ ì»´í¬ë„ŒíŠ¸
 */
@Component({
  selector: 'app-message-card',
  templateUrl: './message-card.component.html',
  styleUrls: ['./message-card.component.scss'],
  standalone: false
})
export class MessageCardComponent {
  @Input() message!: SlackMessage;
  @Output() deleted = new EventEmitter<void>();
  @Output() reactionToggled = new EventEmitter<string>();

  isEmojiPickerOpen = false;

  /**
   * ë©”ì‹œì§€ ì‚­ì œ
   */
  deleteMessage(): void {
    this.deleted.emit();
  }

  /**
   * ì´ëª¨ì§€ í”¼ì»¤ í† ê¸€
   */
  toggleEmojiPicker(): void {
    this.isEmojiPickerOpen = !this.isEmojiPickerOpen;
  }

  /**
   * ì´ëª¨ì§€ ì„ íƒ
   */
  onEmojiSelected(event: any): void {
    this.reactionToggled.emit(event.emoji.native);
    this.isEmojiPickerOpen = false;
  }

  /**
   * ë¦¬ì•¡ì…˜ í´ë¦­
   */
  onReactionClick(emoji: string): void {
    this.reactionToggled.emit(emoji);
  }

  /**
   * ë¦¬ì•¡ì…˜ ê·¸ë£¹í™”
   */
  groupReactions(): { emoji: string; count: number }[] {
    const grouped: Map<string, number> = new Map();

    this.message.reactions?.forEach(reaction => {
      grouped.set(reaction.emoji, (grouped.get(reaction.emoji) || 0) + 1);
    });

    return Array.from(grouped.entries()).map(([emoji, count]) => ({ emoji, count }));
  }
}
