import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { ChatTalkSocketService } from '../../chat-talk/shared/services/chat-talk-socket.service';
import { SlackMessage, SlackChannel, SlackReaction } from '../models/slack.interface';

/**
 * Slack Socket Adapter
 * ê¸°ì¡´ ChatTalkSocketServiceë¥¼ Slack ëª¨ë“ˆì—ì„œ ì‚¬ìš©í•˜ê¸° ìœ„í•œ ì–´ëŒ‘í„°
 * 
 * ì¥ì :
 * 1. ë‹¨ì¼ Socket ì—°ê²° ì‚¬ìš© (ë¦¬ì†ŒìŠ¤ ì ˆì•½)
 * 2. ê¸°ì¡´ ì¸í”„ë¼ ì¬ì‚¬ìš©
 * 3. ì¼ê´€ëœ Socket ê´€ë¦¬
 */
@Injectable({
  providedIn: 'root'
})
export class SlackSocketAdapter {
  // ë©”ì‹œì§€ ìŠ¤íŠ¸ë¦¼
  private messageReceived$ = new Subject<SlackMessage>();
  private messageDeleted$ = new Subject<string>();
  private messageUpdated$ = new Subject<SlackMessage>();
  
  // ë¦¬ì•¡ì…˜ ìŠ¤íŠ¸ë¦¼
  private reactionAdded$ = new Subject<{ messageId: string; reaction: SlackReaction }>();
  private reactionRemoved$ = new Subject<{ messageId: string; emoji: string; userId: string }>();
  
  // ì±„ë„ ìŠ¤íŠ¸ë¦¼
  private channelCreated$ = new Subject<SlackChannel>();
  private channelUpdated$ = new Subject<SlackChannel>();
  
  // íƒ€ì´í•‘ ì¸ë””ì¼€ì´í„°
  private userTyping$ = new Subject<{ channelId: string; userId: string; isTyping: boolean }>();
  
  // ì˜¨ë¼ì¸ ì‚¬ìš©ì ì¶”ì 
  private onlineUsers$ = new Subject<number[]>();
  private userOnline$ = new Subject<number>();
  private userOffline$ = new Subject<number>();
  private onlineUserIds: Set<number> = new Set();

  constructor(private chatTalkSocket: ChatTalkSocketService) {
    this.setupSlackEventListeners();
  }

  /**
   * Slack ì „ìš© ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì„¤ì •
   */
  private setupSlackEventListeners(): void {
    // ë©”ì‹œì§€ ì´ë²¤íŠ¸
    this.chatTalkSocket.listenEvent('slack:message:received', (message: SlackMessage) => {
      this.messageReceived$.next(message);
    });

    this.chatTalkSocket.listenEvent('slack:message:deleted', (messageId: string) => {
      this.messageDeleted$.next(messageId);
    });

    this.chatTalkSocket.listenEvent('slack:message:updated', (message: SlackMessage) => {
      this.messageUpdated$.next(message);
    });

    // ë¦¬ì•¡ì…˜ ì´ë²¤íŠ¸
    this.chatTalkSocket.listenEvent('slack:reaction:added', (data: { messageId: string; reaction: SlackReaction }) => {
      this.reactionAdded$.next(data);
    });

    this.chatTalkSocket.listenEvent('slack:reaction:removed', (data: { messageId: string; emoji: string; userId: string }) => {
      this.reactionRemoved$.next(data);
    });

    // ì±„ë„ ì´ë²¤íŠ¸
    this.chatTalkSocket.listenEvent('slack:channel:created', (channel: SlackChannel) => {
      this.channelCreated$.next(channel);
    });

    this.chatTalkSocket.listenEvent('slack:channel:updated', (channel: SlackChannel) => {
      this.channelUpdated$.next(channel);
    });

    // íƒ€ì´í•‘ ì´ë²¤íŠ¸
    this.chatTalkSocket.listenEvent('slack:user:typing', (data: { channelId: string; userId: string; isTyping: boolean }) => {
      this.userTyping$.next(data);
    });

    // ì˜¨ë¼ì¸ ìƒíƒœ ì´ë²¤íŠ¸
    this.chatTalkSocket.listenEvent('slack:users:online', (userIds: number[]) => {
      this.onlineUserIds = new Set(userIds);
      this.onlineUsers$.next(userIds);
    });

    this.chatTalkSocket.listenEvent('slack:user:online', (userId: number) => {
      this.onlineUserIds.add(userId);
      this.userOnline$.next(userId);
      this.onlineUsers$.next(Array.from(this.onlineUserIds));
    });

    this.chatTalkSocket.listenEvent('slack:user:offline', (userId: number) => {
      this.onlineUserIds.delete(userId);
      this.userOffline$.next(userId);
      this.onlineUsers$.next(Array.from(this.onlineUserIds));
    });
  }

  /**
   * Socket ì—°ê²° (ê¸°ì¡´ ChatTalkSocket ì‚¬ìš©)
   */
  async connect(userId: string): Promise<void> {
    await this.chatTalkSocket.connect();
    // Slack ì‚¬ìš©ì ë“±ë¡ (ChatTalkê³¼ ë‹¤ë¥¸ ë„¤ì„ìŠ¤í˜ì´ìŠ¤ ì‚¬ìš© ê°€ëŠ¥)
    this.chatTalkSocket.emitEvent('slack:register:user', { userId });
  }

  /**
   * Socket ì—°ê²° í•´ì œ
   */
  disconnect(): void {
    // ChatTalkê³¼ ê³µìœ í•˜ë¯€ë¡œ ì‹¤ì œë¡œ disconnectí•˜ì§€ ì•ŠìŒ
    // ëŒ€ì‹  Slack ì´ë²¤íŠ¸ë§Œ êµ¬ë… í•´ì œ
  }

  /**
   * ì±„ë„ ì°¸ì—¬
   */
  joinChannel(channelId: string): void {
    this.chatTalkSocket.emitEvent('slack:channel:join', { channelId });
  }

  /**
   * ì±„ë„ ë‚˜ê°€ê¸°
   */
  leaveChannel(channelId: string): void {
    this.chatTalkSocket.emitEvent('slack:channel:leave', { channelId });
  }

  /**
   * ë©”ì‹œì§€ ì „ì†¡
   */
  sendMessage(channelId: string, text: string, files?: string[]): void {
    this.chatTalkSocket.emitEvent('slack:message:send', {
      channelId,
      text,
      files,
      timestamp: new Date()
    });
  }

  /**
   * ë©”ì‹œì§€ ì‚­ì œ
   */
  deleteMessage(messageId: string): void {
    this.chatTalkSocket.emitEvent('slack:message:delete', { messageId });
  }

  /**
   * ë©”ì‹œì§€ ìˆ˜ì •
   */
  updateMessage(messageId: string, text: string): void {
    this.chatTalkSocket.emitEvent('slack:message:update', { messageId, text });
  }

  /**
   * ë¦¬ì•¡ì…˜ í† ê¸€
   */
  toggleReaction(messageId: string, emoji: string): void {
    this.chatTalkSocket.emitEvent('slack:reaction:toggle', { messageId, emoji });
  }

  /**
   * íƒ€ì´í•‘ ìƒíƒœ ì „ì†¡
   */
  sendTypingStatus(channelId: string, isTyping: boolean): void {
    this.chatTalkSocket.emitEvent('slack:user:typing', { channelId, isTyping });
  }

  /**
   * íŠ¹ì • ì‚¬ìš©ìê°€ ì˜¨ë¼ì¸ì¸ì§€ í™•ì¸
   */
  isUserOnline(userId: number): boolean {
    return this.onlineUserIds.has(userId);
  }

  /**
   * ì˜¨ë¼ì¸ ì‚¬ìš©ì ID ëª©ë¡ ë°˜í™˜
   */
  getOnlineUserIds(): number[] {
    return Array.from(this.onlineUserIds);
  }

  /**
   * ì—°ê²° ìƒíƒœ í™•ì¸
   */
  get isConnected$(): Observable<boolean> {
    // ChatTalkSocketì˜ ì—°ê²° ìƒíƒœë¥¼ Observableë¡œ ë°˜í™˜
    return new Observable(observer => {
      const checkConnection = () => {
        observer.next(this.chatTalkSocket.isConnected());
      };
      
      checkConnection();
      const interval = setInterval(checkConnection, 1000);
      
      return () => clearInterval(interval);
    });
  }

  // Observable Getters
  get onMessageReceived$(): Observable<SlackMessage> {
    return this.messageReceived$.asObservable();
  }

  get onMessageDeleted$(): Observable<string> {
    return this.messageDeleted$.asObservable();
  }

  get onMessageUpdated$(): Observable<SlackMessage> {
    return this.messageUpdated$.asObservable();
  }

  get onReactionAdded$(): Observable<{ messageId: string; reaction: SlackReaction }> {
    return this.reactionAdded$.asObservable();
  }

  get onReactionRemoved$(): Observable<{ messageId: string; emoji: string; userId: string }> {
    return this.reactionRemoved$.asObservable();
  }

  get onChannelCreated$(): Observable<SlackChannel> {
    return this.channelCreated$.asObservable();
  }

  get onChannelUpdated$(): Observable<SlackChannel> {
    return this.channelUpdated$.asObservable();
  }

  get onUserTyping$(): Observable<{ channelId: string; userId: string; isTyping: boolean }> {
    return this.userTyping$.asObservable();
  }

  get onOnlineUsersChanged$(): Observable<number[]> {
    return this.onlineUsers$.asObservable();
  }

  get onUserOnline$(): Observable<number> {
    return this.userOnline$.asObservable();
  }

  get onUserOffline$(): Observable<number> {
    return this.userOffline$.asObservable();
  }
}
