import { Component, OnInit, OnDestroy } from '@angular/core';
import { ChannelViewModel } from '../../viewmodels/channel.viewmodel';
import { MessageViewModel } from '../../viewmodels/message.viewmodel';
import { SlackSocketAdapter } from '../../services/slack-socket.adapter';
import { SlackApiService } from '../../services/slack-api.service';
import { SlackAuthService } from '../../services/slack-auth.service';
import { Subject, takeUntil } from 'rxjs';
import { SlackUser } from '../../models/slack.interface';

/**
 * í˜‘ì—… ì±— ë©”ì¸ í˜ì´ì§€
 * ì „ì²´ ë ˆì´ì•„ì›ƒ ê´€ë¦¬
 */
@Component({
  selector: 'app-slack-main',
  templateUrl: './slack-main.component.html',
  styleUrls: ['./slack-main.component.scss'],
  standalone: false,
  providers: [ChannelViewModel, MessageViewModel]
})
export class SlackMainComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();
  
  isConnected = false;
  isSidebarOpen = true;
  isMembersSidebarOpen = true;
  members: SlackUser[] = [];

  constructor(
    public channelVM: ChannelViewModel,
    public messageVM: MessageViewModel,
    private socketAdapter: SlackSocketAdapter,
    private slackApi: SlackApiService,
    private slackAuthService: SlackAuthService
  ) {}

  ngOnInit(): void {
    // Socket ì—°ê²° ìƒíƒœ êµ¬ë…
    this.socketAdapter.isConnected$
      .pipe(takeUntil(this.destroy$))
      .subscribe(connected => {
        this.isConnected = connected;
      });

    // ì˜¨ë¼ì¸ ì‚¬ìš©ì ë³€ê²½ êµ¬ë…
    this.socketAdapter.onOnlineUsersChanged$
      .pipe(takeUntil(this.destroy$))
      .subscribe(onlineUserIds => {
        // ë©¤ë²„ ëª©ë¡ ì—…ë°ì´íŠ¸ (ì˜¨ë¼ì¸ ìƒíƒœ ë°˜ì˜)
        this.updateMembersOnlineStatus();
      });

    // ê°œë³„ ì‚¬ìš©ì ì˜¨ë¼ì¸ ì´ë²¤íŠ¸
    this.socketAdapter.onUserOnline$
      .pipe(takeUntil(this.destroy$))
      .subscribe(userId => {
        this.updateMembersOnlineStatus();
      });

    // ê°œë³„ ì‚¬ìš©ì ì˜¤í”„ë¼ì¸ ì´ë²¤íŠ¸
    this.socketAdapter.onUserOffline$
      .pipe(takeUntil(this.destroy$))
      .subscribe(userId => {
        this.updateMembersOnlineStatus();
      });

    // ì‚¬ìš©ì IDë¡œ Socket ì—°ê²° (TODO: Auth Serviceì—ì„œ ê°€ì ¸ì˜¤ê¸°)
    const userId = 'user-123'; // ì„ì‹œ
    this.socketAdapter.connect(userId);

    // ë©¤ë²„ ëª©ë¡ ë¡œë“œ
    this.loadMembers();

    // ì±„ë„ ëª©ë¡ ë¡œë“œ
    this.channelVM.loadChannels();

    // í™œì„± ì±„ë„ ë³€ê²½ ì‹œ ë©”ì‹œì§€ ë¡œë“œ
    this.channelVM.getActiveChannel()
      .pipe(takeUntil(this.destroy$))
      .subscribe(channel => {
        if (channel) {
          this.messageVM.loadMessages(channel.id, true);
        }
      });
  }

  /**
   * ë©¤ë²„ ëª©ë¡ ë¡œë“œ
   */
  private loadMembers(): void {
    this.slackApi.getUsers()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (users) => {
          this.members = users;
          this.updateMembersOnlineStatus();
        },
        error: (error) => {
        }
      });
  }

  /**
   * ë©¤ë²„ì˜ ì˜¨ë¼ì¸ ìƒíƒœ ì—…ë°ì´íŠ¸
   */
  private updateMembersOnlineStatus(): void {
    if (!this.members || this.members.length === 0) return;
    
    // ê° ë©¤ë²„ì˜ ì˜¨ë¼ì¸ ìƒíƒœë¥¼ ì‹¤ì‹œê°„ìœ¼ë¡œ ì—…ë°ì´íŠ¸
    // ì°¸ê³ : members ë°°ì—´ì„ ìƒˆë¡œ ìƒì„±í•˜ì—¬ Angularì˜ ë³€ê²½ ê°ì§€ íŠ¸ë¦¬ê±°
    this.members = this.members.map(member => ({
      ...member,
      isOnline: this.socketAdapter.isUserOnline(Number(member.id))
    }));
  }

  /**
   * ì‚¬ì´ë“œë°” í† ê¸€
   */
  toggleSidebar(): void {
    this.isSidebarOpen = !this.isSidebarOpen;
  }

  /**
   * ë©¤ë²„ ì‚¬ì´ë“œë°” í† ê¸€
   */
  toggleMembersSidebar(): void {
    this.isMembersSidebarOpen = !this.isMembersSidebarOpen;
  }

  /**
   * ë¡œê·¸ì•„ì›ƒ
   */
  logout(): void {
    this.slackAuthService.logout();
  }

  ngOnDestroy(): void {
    this.socketAdapter.disconnect();
    this.destroy$.next();
    this.destroy$.complete();
  }
}
