import { Component, Input, Output, EventEmitter, ViewChild, ElementRef, AfterViewChecked } from '@angular/core';
import { SlackMessage, SlackChannel } from '../../models/slack.interface';

/**
 * ë©”ì‹œì§€ ë¦¬ìŠ¤íŠ¸ ì»´í¬ë„ŒíŠ¸
 */
@Component({
  selector: 'app-message-list',
  templateUrl: './message-list.component.html',
  styleUrls: ['./message-list.component.scss'],
  standalone: false
})
export class MessageListComponent implements AfterViewChecked {
  @Input() messages: SlackMessage[] | null = [];
  @Input() isLoading: boolean | null = false;
  @Input() activeChannel: SlackChannel | null = null;

  @Output() messageDeleted = new EventEmitter<string>();
  @Output() reactionToggled = new EventEmitter<{ messageId: string; emoji: string }>();

  @ViewChild('scrollContainer') scrollContainer!: ElementRef;
  private shouldScrollToBottom = true;

  ngAfterViewChecked(): void {
    if (this.shouldScrollToBottom) {
      this.scrollToBottom();
    }
  }

  /**
   * ìŠ¤í¬ë¡¤ì„ í•˜ë‹¨ìœ¼ë¡œ ì´ë™
   */
  private scrollToBottom(): void {
    try {
      this.scrollContainer.nativeElement.scrollTop = 
        this.scrollContainer.nativeElement.scrollHeight;
    } catch (err) {
    }
  }

  /**
   * ìŠ¤í¬ë¡¤ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬
   */
  onScroll(event: Event): void {
    const element = event.target as HTMLElement;
    const atBottom = Math.abs(
      element.scrollHeight - element.scrollTop - element.clientHeight
    ) < 10;
    
    this.shouldScrollToBottom = atBottom;
  }

  /**
   * ë©”ì‹œì§€ ì‚­ì œ
   */
  deleteMessage(messageId: string): void {
    this.messageDeleted.emit(messageId);
  }

  /**
   * ë¦¬ì•¡ì…˜ í† ê¸€
   */
  toggleReaction(messageId: string, emoji: string): void {
    this.reactionToggled.emit({ messageId, emoji });
  }
}
