import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { SlackAuthService } from '../services/slack-auth.service';

@Injectable({
  providedIn: 'root'
})
export class SlackAuthGuard implements CanActivate {
  constructor(
    private slackAuthService: SlackAuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    
    if (this.slackAuthService.isAuthenticated()) {
      return true;
    }

    // ë¡œê·¸ì¸ë˜ì–´ ìˆì§€ ì•Šìœ¼ë©´ ë¡œê·¸ì¸ í˜ì´ì§€ë¡œ ë¦¬ë‹¤ì´ë ‰íŠ¸
    return this.router.createUrlTree(['/slack/login'], {
      queryParams: { returnUrl: state.url }
    });
  }
}
