import { Injectable, OnDestroy } from '@angular/core';
import { BehaviorSubject, Observable, Subject, takeUntil, debounceTime } from 'rxjs';
import { SlackMessage, SlackReaction, SlackFile } from '../models/slack.interface';
import { SlackApiService } from '../services/slack-api.service';
import { SlackSocketAdapter } from '../services/slack-socket.adapter';
import { FormControl, Validators } from '@angular/forms';

/**
 * Message ViewModel
 * MVVM íŒ¨í„´ì˜ ViewModel ë ˆì´ì–´
 * ë©”ì‹œì§€ ê´€ë ¨ ë¹„ì¦ˆë‹ˆìŠ¤ ë¡œì§ ê´€ë¦¬
 */
@Injectable()
export class MessageViewModel implements OnDestroy {
  private destroy$ = new Subject<void>();

  // ìƒíƒœ ê´€ë¦¬
  private messages$ = new BehaviorSubject<SlackMessage[]>([]);
  private isLoading$ = new BehaviorSubject<boolean>(false);
  private isFileUploading$ = new BehaviorSubject<boolean>(false);
  private uploadedFiles$ = new BehaviorSubject<SlackFile[]>([]);
  
  // íƒ€ì´í•‘ ì¸ë””ì¼€ì´í„°
  private typingUsers$ = new BehaviorSubject<Set<string>>(new Set());

  // Form Control
  messageForm = new FormControl('', [Validators.required, Validators.maxLength(5000)]);

  // í˜ì´ì§€ë„¤ì´ì…˜
  private currentPage = 1;
  private hasMore = true;

  constructor(
    private apiService: SlackApiService,
    private socketAdapter: SlackSocketAdapter
  ) {
    this.initializeSocketListeners();
    this.initializeTypingHandler();
  }

  /**
   * Socket ì´ë²¤íŠ¸ ë¦¬ìŠ¤ë„ˆ ì´ˆê¸°í™”
   */
  private initializeSocketListeners(): void {
    // ë©”ì‹œì§€ ìˆ˜ì‹ 
    this.socketAdapter.onMessageReceived$
      .pipe(takeUntil(this.destroy$))
      .subscribe(message => {
        const current = this.messages$.value;
        this.messages$.next([...current, message]);
      });

    // ë©”ì‹œì§€ ì‚­ì œ
    this.socketAdapter.onMessageDeleted$
      .pipe(takeUntil(this.destroy$))
      .subscribe(messageId => {
        const current = this.messages$.value;
        this.messages$.next(current.filter(m => m.id !== messageId));
      });

    // ë©”ì‹œì§€ ì—…ë°ì´íŠ¸
    this.socketAdapter.onMessageUpdated$
      .pipe(takeUntil(this.destroy$))
      .subscribe(updatedMessage => {
        const current = this.messages$.value;
        const index = current.findIndex(m => m.id === updatedMessage.id);
        
        if (index !== -1) {
          const updated = [...current];
          updated[index] = updatedMessage;
          this.messages$.next(updated);
        }
      });

    // ë¦¬ì•¡ì…˜ ì¶”ê°€
    this.socketAdapter.onReactionAdded$
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ messageId, reaction }) => {
        this.addReactionToMessage(messageId, reaction);
      });

    // ë¦¬ì•¡ì…˜ ì œê±°
    this.socketAdapter.onReactionRemoved$
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ messageId, emoji, userId }) => {
        this.removeReactionFromMessage(messageId, emoji, userId);
      });

    // íƒ€ì´í•‘ ì¸ë””ì¼€ì´í„°
    this.socketAdapter.onUserTyping$
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ userId, isTyping }) => {
        const current = new Set(this.typingUsers$.value);
        
        if (isTyping) {
          current.add(userId);
        } else {
          current.delete(userId);
        }
        
        this.typingUsers$.next(current);
      });
  }

  /**
   * íƒ€ì´í•‘ í•¸ë“¤ëŸ¬ ì´ˆê¸°í™” (debounce ì ìš©)
   */
  private initializeTypingHandler(): void {
    this.messageForm.valueChanges
      .pipe(
        debounceTime(300),
        takeUntil(this.destroy$)
      )
      .subscribe(value => {
        // í˜„ì¬ ì±„ë„ë¡œ íƒ€ì´í•‘ ìƒíƒœ ì „ì†¡ (channelIdëŠ” ì™¸ë¶€ì—ì„œ ì£¼ì… í•„ìš”)
        // this.socketService.sendTypingStatus(channelId, !!value);
      });
  }

  /**
   * ë©”ì‹œì§€ ë¡œë“œ (í˜ì´ì§€ë„¤ì´ì…˜)
   */
  loadMessages(channelId: string, reset: boolean = false): void {
    if (!this.hasMore && !reset) return;

    if (reset) {
      this.currentPage = 1;
      this.messages$.next([]);
    }

    this.isLoading$.next(true);

    this.apiService.getMessages(channelId, this.currentPage, 50)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: ({ messages, hasMore }) => {
          const current = this.messages$.value;
          // ê³¼ê±° ë©”ì‹œì§€ë¥¼ ì•ì— ì¶”ê°€ (ì—­ìˆœ)
          this.messages$.next([...messages.reverse(), ...current]);
          this.hasMore = hasMore;
          this.currentPage++;
          this.isLoading$.next(false);
        },
        error: (error) => {
          this.isLoading$.next(false);
        }
      });
  }

  /**
   * ë©”ì‹œì§€ ì „ì†¡
   */
  sendMessage(channelId: string): void {
    const text = this.messageForm.value?.trim();
    if (!text && this.uploadedFiles$.value.length === 0) return;

    const files = this.uploadedFiles$.value.map(f => f.id);

    this.socketAdapter.sendMessage(channelId, text || '', files);

    // ì…ë ¥ì°½ ì´ˆê¸°í™”
    this.messageForm.reset();
    this.uploadedFiles$.next([]);
  }

  /**
   * ë©”ì‹œì§€ ì‚­ì œ
   */
  deleteMessage(messageId: string): void {
    this.socketAdapter.deleteMessage(messageId);
  }

  /**
   * ë©”ì‹œì§€ ìˆ˜ì •
   */
  updateMessage(messageId: string, text: string): void {
    this.socketAdapter.updateMessage(messageId, text);
  }

  /**
   * ë¦¬ì•¡ì…˜ í† ê¸€
   */
  toggleReaction(messageId: string, emoji: string): void {
    this.socketAdapter.toggleReaction(messageId, emoji);
  }

  /**
   * íŒŒì¼ ì—…ë¡œë“œ
   */
  uploadFiles(channelId: string, files: FileList): void {
    if (files.length === 0) return;

    this.isFileUploading$.next(true);

    this.apiService.uploadFiles(channelId, files)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (uploadedFiles) => {
          const current = this.uploadedFiles$.value;
          this.uploadedFiles$.next([...current, ...uploadedFiles]);
          this.isFileUploading$.next(false);
        },
        error: (error) => {
          this.isFileUploading$.next(false);
        }
      });
  }

  /**
   * ì—…ë¡œë“œëœ íŒŒì¼ ì œê±°
   */
  removeUploadedFile(fileId: string): void {
    const current = this.uploadedFiles$.value;
    this.uploadedFiles$.next(current.filter(f => f.id !== fileId));
  }

  /**
   * ë©”ì‹œì§€ì— ë¦¬ì•¡ì…˜ ì¶”ê°€ (ë‚´ë¶€ ë©”ì„œë“œ)
   */
  private addReactionToMessage(messageId: string, reaction: SlackReaction): void {
    const current = this.messages$.value;
    const index = current.findIndex(m => m.id === messageId);
    
    if (index !== -1) {
      const updated = [...current];
      const message = { ...updated[index] };
      message.reactions = [...message.reactions, reaction];
      updated[index] = message;
      this.messages$.next(updated);
    }
  }

  /**
   * ë©”ì‹œì§€ì—ì„œ ë¦¬ì•¡ì…˜ ì œê±° (ë‚´ë¶€ ë©”ì„œë“œ)
   */
  private removeReactionFromMessage(messageId: string, emoji: string, userId: string): void {
    const current = this.messages$.value;
    const index = current.findIndex(m => m.id === messageId);
    
    if (index !== -1) {
      const updated = [...current];
      const message = { ...updated[index] };
      message.reactions = message.reactions.filter(
        r => !(r.emoji === emoji && r.userId === userId)
      );
      updated[index] = message;
      this.messages$.next(updated);
    }
  }

  /**
   * ë¦¬ì•¡ì…˜ ê·¸ë£¹í™”
   */
  groupReactions(reactions: SlackReaction[]): { emoji: string; count: number; userIds: string[] }[] {
    const grouped: Map<string, { count: number; userIds: string[] }> = new Map();

    reactions.forEach(reaction => {
      if (!grouped.has(reaction.emoji)) {
        grouped.set(reaction.emoji, { count: 0, userIds: [] });
      }
      const group = grouped.get(reaction.emoji)!;
      group.count++;
      group.userIds.push(reaction.userId);
    });

    return Array.from(grouped.entries()).map(([emoji, data]) => ({
      emoji,
      count: data.count,
      userIds: data.userIds
    }));
  }

  /**
   * í˜„ì¬ ì‚¬ìš©ìê°€ ë¦¬ì•¡ì…˜í–ˆëŠ”ì§€ í™•ì¸
   */
  hasUserReacted(reactions: SlackReaction[], emoji: string, userId: string): boolean {
    return reactions.some(r => r.emoji === emoji && r.userId === userId);
  }

  // Getters
  getMessages(): Observable<SlackMessage[]> {
    return this.messages$.asObservable();
  }

  getIsLoading(): Observable<boolean> {
    return this.isLoading$.asObservable();
  }

  getIsFileUploading(): Observable<boolean> {
    return this.isFileUploading$.asObservable();
  }

  getUploadedFiles(): Observable<SlackFile[]> {
    return this.uploadedFiles$.asObservable();
  }

  getTypingUsers(): Observable<Set<string>> {
    return this.typingUsers$.asObservable();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
