import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SlackMainComponent } from './pages/slack-main/slack-main.component';
import { SlackLoginComponent } from './components/slack-login/slack-login.component';
import { SlackAuthGuard } from './guards/slack-auth.guard';

const routes: Routes = [
  {
    path: 'login',
    component: SlackLoginComponent
  },
  {
    path: '',
    component: SlackMainComponent,
    canActivate: [SlackAuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SlackRoutingModule { }
