import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Subject, takeUntil } from 'rxjs';
import { SlackUser } from '../../models/slack.interface';
import { SlackApiService } from '../../services/slack-api.service';
import { DebugLoggerService } from '../../../../shared/services/debug-logger.service';

/**
 * ì±„ë„ ìƒì„± ë‹¤ì´ì–¼ë¡œê·¸
 * Store íšŒì›ì„ Slack ë©¤ë²„ë¡œ ì´ˆëŒ€
 */
@Component({
  selector: 'app-create-channel-dialog',
  templateUrl: './create-channel-dialog.component.html',
  styleUrls: ['./create-channel-dialog.component.scss'],
  standalone: false
})
export class CreateChannelDialogComponent implements OnInit, OnDestroy {
  private destroy$ = new Subject<void>();

  channelForm: FormGroup;
  allUsers: SlackUser[] = [];
  selectedMembers: SlackUser[] = [];
  isLoading = false;
  searchQuery = '';

  constructor(
    private fb: FormBuilder,
    private dialogRef: MatDialogRef<CreateChannelDialogComponent>,
    private slackApiService: SlackApiService,
    private debugLogger: DebugLoggerService
  ) {
    this.debugLogger.info('SlackDialog', 'ğŸ¨ ì±„ë„ ìƒì„± ë‹¤ì´ì–¼ë¡œê·¸ ì—´ë¦¼');
    this.channelForm = this.fb.group({
      name: ['', [Validators.required, Validators.minLength(2)]],
      description: [''],
      isPrivate: [false]
    });
  }

  ngOnInit(): void {
    this.debugLogger.info('SlackDialog', 'ğŸ”„ ë‹¤ì´ì–¼ë¡œê·¸ ì´ˆê¸°í™”');
    this.loadUsers();
  }

  /**
   * Store íšŒì› ëª©ë¡ ë¡œë“œ
   */
  loadUsers(): void {
    this.debugLogger.info('SlackDialog', 'ğŸ“ Store íšŒì› ëª©ë¡ ë¡œë“œ ì‹œì‘');
    this.isLoading = true;
    
    this.slackApiService.getUsers()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (users) => {
          this.debugLogger.success('SlackDialog', `âœ… íšŒì› ë¡œë“œ ì„±ê³µ: ${users.length}ëª…`);
          this.debugLogger.info('SlackDialog', `íšŒì› ëª©ë¡: ${JSON.stringify(users.map(u => ({ id: u.id, name: u.name })))}`);
          this.allUsers = users;
          this.isLoading = false;
        },
        error: (error) => {
          this.debugLogger.error('SlackDialog', `âŒ íšŒì› ë¡œë“œ ì‹¤íŒ¨: ${error.message}`);
          this.debugLogger.error('SlackDialog', `ì—ëŸ¬ ì „ì²´: ${JSON.stringify(error)}`);
          this.isLoading = false;
        }
      });
  }

  /**
   * ë©¤ë²„ ì„ íƒ/í•´ì œ í† ê¸€
   */
  toggleMember(user: SlackUser): void {
    const index = this.selectedMembers.findIndex(m => m.id === user.id);
    if (index > -1) {
      this.debugLogger.info('SlackDialog', `â– ë©¤ë²„ ì„ íƒ í•´ì œ: ${user.name} (${user.id})`);
      this.selectedMembers.splice(index, 1);
    } else {
      this.debugLogger.info('SlackDialog', `â• ë©¤ë²„ ì„ íƒ: ${user.name} (${user.id})`);
      this.selectedMembers.push(user);
    }
    this.debugLogger.info('SlackDialog', `í˜„ì¬ ì„ íƒëœ ë©¤ë²„ ìˆ˜: ${this.selectedMembers.length}ëª…`);
  }

  /**
   * ë©¤ë²„ê°€ ì„ íƒë˜ì—ˆëŠ”ì§€ í™•ì¸
   */
  isMemberSelected(userId: string): boolean {
    return this.selectedMembers.some(m => m.id === userId);
  }

  /**
   * ê²€ìƒ‰ í•„í„°ë§ëœ ì‚¬ìš©ì ëª©ë¡
   */
  get filteredUsers(): SlackUser[] {
    if (!this.searchQuery) {
      return this.allUsers;
    }
    
    const query = this.searchQuery.toLowerCase();
    return this.allUsers.filter(user =>
      user.name.toLowerCase().includes(query) ||
      user.email.toLowerCase().includes(query)
    );
  }

  /**
   * ì±„ë„ ìƒì„±
   */
  createChannel(): void {
    if (this.channelForm.invalid) {
      this.debugLogger.warn('SlackDialog', 'âš ï¸ í¼ ìœ íš¨ì„± ê²€ì‚¬ ì‹¤íŒ¨');
      this.debugLogger.info('SlackDialog', `í¼ ì—ëŸ¬: ${JSON.stringify(this.channelForm.errors)}`);
      return;
    }

    const formValue = this.channelForm.value;
    const result = {
      name: formValue.name,
      description: formValue.description || '',
      isPrivate: formValue.isPrivate,
      members: this.selectedMembers.map(m => m.id)
    };

    this.debugLogger.info('SlackDialog', 'âœ… ì±„ë„ ìƒì„± ë°ì´í„° ì¤€ë¹„ ì™„ë£Œ');
    this.debugLogger.info('SlackDialog', `ì±„ë„ëª…: ${result.name}`);
    this.debugLogger.info('SlackDialog', `ì„¤ëª…: ${result.description}`);
    this.debugLogger.info('SlackDialog', `ë¹„ê³µê°œ: ${result.isPrivate}`);
    this.debugLogger.info('SlackDialog', `ì„ íƒëœ ë©¤ë²„ ìˆ˜: ${result.members.length}ëª…`);
    this.debugLogger.info('SlackDialog', `ë©¤ë²„ IDs: ${JSON.stringify(result.members)}`);
    this.debugLogger.info('SlackDialog', `ì „ì†¡ ë°ì´í„°: ${JSON.stringify(result)}`);

    this.dialogRef.close(result);
  }

  /**
   * ì·¨ì†Œ
   */
  cancel(): void {
    this.debugLogger.info('SlackDialog', 'âŒ ì±„ë„ ìƒì„± ì·¨ì†Œ');
    this.dialogRef.close();
  }

  ngOnDestroy(): void {
    this.debugLogger.info('SlackDialog', 'ğŸ”š ë‹¤ì´ì–¼ë¡œê·¸ ì¢…ë£Œ');
    this.destroy$.next();
    this.destroy$.complete();
  }
}
