import { Component, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { FormControl } from '@angular/forms';
import { SlackFile, SlackChannel } from '../../models/slack.interface';

/**
 * ë©”ì‹œì§€ ì…ë ¥ ì»´í¬ë„ŒíŠ¸
 */
@Component({
  selector: 'app-message-input',
  templateUrl: './message-input.component.html',
  styleUrls: ['./message-input.component.scss'],
  standalone: false
})
export class MessageInputComponent {
  @Input() messageForm!: FormControl;
  @Input() uploadedFiles: SlackFile[] | null = [];
  @Input() isUploading: boolean | null = false;
  @Input() activeChannel: SlackChannel | null = null;

  @Output() messageSent = new EventEmitter<string>();
  @Output() filesUploaded = new EventEmitter<{ channelId: string; files: FileList }>();
  @Output() fileRemoved = new EventEmitter<string>();

  @ViewChild('fileInput') fileInput!: ElementRef<HTMLInputElement>;

  /**
   * íŒŒì¼ ì„ íƒ íŠ¸ë¦¬ê±°
   */
  triggerFileUpload(): void {
    this.fileInput.nativeElement.click();
  }

  /**
   * íŒŒì¼ ì„ íƒ í•¸ë“¤ëŸ¬
   */
  onFileSelected(event: Event): void {
    const files = (event.target as HTMLInputElement).files;
    
    if (files && files.length > 0 && this.activeChannel) {
      this.filesUploaded.emit({
        channelId: this.activeChannel.id,
        files
      });
      
      // ì…ë ¥ ì´ˆê¸°í™”
      this.fileInput.nativeElement.value = '';
    }
  }

  /**
   * ì—…ë¡œë“œëœ íŒŒì¼ ì œê±°
   */
  removeFile(fileId: string): void {
    this.fileRemoved.emit(fileId);
  }

  /**
   * í‚¤ë³´ë“œ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬
   */
  onKeyDown(event: KeyboardEvent): void {
    // ì¡°í•© ì¤‘ì´ë©´ ë¬´ì‹œ
    if (event.isComposing) return;

    // Enter (Shift ì—†ì´)
    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault();
      this.sendMessage();
    }
  }

  /**
   * ë©”ì‹œì§€ ì „ì†¡
   */
  sendMessage(): void {
    if (!this.activeChannel) return;
    
    const text = this.messageForm.value?.trim();
    if (!text && (!this.uploadedFiles || this.uploadedFiles.length === 0)) return;

    this.messageSent.emit(this.activeChannel.id);
  }

  /**
   * ì „ì†¡ ë²„íŠ¼ ë¹„í™œì„±í™” ì—¬ë¶€
   */
  isSendDisabled(): boolean {
    const text = this.messageForm.value?.trim();
    return !text && (!this.uploadedFiles || this.uploadedFiles.length === 0);
  }
}
